header.main_display {
  margin-bottom: 10px;
}
header.main_display .nav-menu {
  position: fixed;
  z-index: 50;
  right: 0;
  top: 0;
  transition: background-color 1s;
  width: 100%;
}
header.main_display .nav-menu.show {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}
header.main_display .nav-menu button {
  background-color: #343a40;
  margin-left: auto;
  margin-right: 1rem;
  margin-top: 1rem;
}
header.main_display .nav-menu button .lines {
  width: 30px;
  height: 30px;
  padding: 3px 0;
  justify-content: space-between;
  align-items: center;
}
header.main_display .nav-menu button .lines .line {
  width: 30px;
  height: 2px;
  background-color: #9A9DA0;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: transform .5s, opacity .5s;
  transition: transform .5s, opacity .5s, -webkit-transform .5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}
header.main_display .nav-menu button .lines .line-first.x {
  -webkit-transform-origin: center center;
  transform-origin: center center;
  -webkit-transform: translateY(11px) rotate(45deg);
  transform: translateY(11px) rotate(45deg);
}
header.main_display .nav-menu button .lines .line-second.x {
  -webkit-transform-origin: center center;
  transform-origin: center center;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
header.main_display .nav-menu button .lines .line-third.x {
  -webkit-transform: translateY(-11px);
  transform: translateY(-11px);
  opacity: 0;
}
header.main_display .nav-menu .navbar ul {
  position: relative;
}
header.main_display .nav-menu .navbar ul li {
  transition: all .3s;
}
header.main_display .nav-menu .navbar ul li a {
  color: white;
  display: block;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 5px;
  position: relative;
  height: 60px;
  outline: none;
  transition: color .5s;
}
header.main_display .nav-menu .navbar ul li:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
header.main_display .nav-menu .navbar ul li:hover a {
  color: #f13a19;
  /* @redIconsColor; */
}
header.main_display .nav-menu .navbar ul li.background {
  width: 200px;
  margin: auto;
}
header.main_display p {
  letter-spacing: 5px;
  font-weight: bold;
  font-size: 15px;
}
header.main_display p.logo-line {
  -webkit-transform: translateX(-2rem);
  transform: translateX(-2rem);
}
header.main_display p img {
  display: inline-block;
  height: 5.5rem;
  width: auto;
}
header.main_display .middle {
  max-width: 80%;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.6);
}
header.main_display .middle h1 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #072704;
}
header.main_display .middle .skills {
  font-size: 1rem;
}
header.main_display .empty {
  height: 10rem;
}
main {
  padding: 15px;
}
main .about_me {
  margin-bottom: 100px;
}
main .about_me h4 {
  color: #072704;
  margin-bottom: 25px;
}
main .about_me .header {
  text-align: center;
}
main .about_me .header div {
  letter-spacing: 4px;
  font-size: 1.2em;
}
main .about_me .header .underline {
  line-height: 3rem;
  border-bottom: 2px solid #000000;
  width: 60%;
  margin: auto;
  padding: 12px;
}
main .about_me div {
  color: #222;
}
main .about_me div.description .article .skills {
  margin-bottom: -23px;
}
main .about_me div.description .article .skills span {
  margin: 0 3px 0 2px ;
  font-size: 6px;
}
main .about_me div.description .article .skills span i {
  -webkit-transform: translateY(-4px);
  transform: translateY(-4px);
}
main .about_me div .photo .wrapper_photo {
  border-radius: 50%;
  border: 1px solid black;
  overflow: hidden;
  max-height: 300px;
  max-width: 300px;
}
main .about_me div .personal span {
  margin-right: 0.6em;
}
main .about_me div .personal div {
  margin-bottom: 10px;
}
main .about_me div .personal div.name {
  margin-bottom: 15px;
  color: #555;
  letter-spacing: 2px;
  font-size: 1.2em;
}
main .about_me div .personal div.skype span {
  color: #00AFF0;
  font-size: 1.3em;
}
main .about_me div .personal div.skype span i {
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
}
main .about_me div .personal div.e-mail span {
  color: #00AFF0;
  font-size: 1.3em;
}
main .about_me div .personal div.e-mail span i {
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
}
main .about_me div .personal div.my_site a {
  color: inherit;
  text-decoration: underline;
}
main .about_me div .personal div.my_site span.html {
  color: #FF6347;
  font-size: 1.3em;
}
main .about_me div .personal div.my_site span.html i {
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
}
main .about_me div .personal div.my_site span.hand {
  color: #00AFF0;
  font-size: 1.3em;
}
main .about_me div .personal div.my_site span.hand i {
  -webkit-transform: translate(-104px, 21px);
  transform: translate(-104px, 21px);
}
main .resume {
  margin-bottom: 150px;
}
main .resume .header {
  text-align: center;
}
main .resume .header div {
  letter-spacing: 4px;
  font-size: 1.2em;
}
main .resume .header div.education {
  text-align: justify;
}
main .resume .header .underline {
  line-height: 3rem;
  border-bottom: 2px solid #000000;
  width: 60%;
  margin: auto;
  padding: 12px;
}
main .resume h2 {
  text-align: center;
}
main .resume .subheader {
  text-align: center;
  font-size: 14px;
  display: inline-block;
  background-color: #FF6347;
  color: white;
  font-weight: 600;
  font-style: italic;
  padding: 0 15px;
  margin: 3rem 0;
}
main .resume .ico {
  text-align: center;
  margin: 25px 0;
}
main .resume .ico i {
  color: #00AFF0;
}
main .portfolio {
  background-color: #222;
  padding-top: 50px;
}
main .portfolio .last-card {
  margin-bottom: 8rem;
}
main .portfolio .card {
  position: relative;
  overflow: hidden;
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
  text-align: center;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  transition: box-shadow 0.3s;
}
main .portfolio .card .card-body {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: opacity .3s;
}
main .portfolio .card .card-body .card-title {
  text-transform: uppercase;
  font-size: 1rem;
  margin-bottom: 1rem;
  color: #FF6347;
}
main .portfolio .card .card-body .wrapper {
  transition: -webkit-transform 0.3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
main .portfolio .card .card-body-2 {
  position: absolute;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
main .portfolio .card .card-body-2 .card-look {
  display: inline-block;
  opacity: 0;
  color: white;
  -webkit-transform: translateY(-100px);
  transform: translateY(-100px);
  transition: opacity 0.3s, color 0.3s, -webkit-transform 0.3s;
  transition: opacity .3s, color .3s, -webkit-transform .3s;
  transition: opacity .3s, color .3s, transform .3s;
  transition: opacity .3s, color .3s, transform .3s, -webkit-transform .3s;
  transition: opacity 0.3s, color 0.3s, transform 0.3s, -webkit-transform 0.3s;
  border: 3px solid #FF6347;
  padding: 5px 20px;
}
main .portfolio .card .card-img-top {
  opacity: .7;
  -webkit-transform: scale(1.2, 1.2);
  transform: scale(1.2, 1.2);
  transition: -webkit-transform 0.3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
main .portfolio .card:hover {
  cursor: pointer;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.9);
}
main .portfolio .card:hover .card-body {
  opacity: 0;
}
main .portfolio .card:hover .card-body .wrapper {
  -webkit-transform: translateY(100px);
  transform: translateY(100px);
}
main .portfolio .card:hover .card-img-top {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}
main .portfolio .card:hover .card-look {
  opacity: 1;
  color: white;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
main .portfolio img {
  width: 100%;
  height: auto;
}
main .portfolio .header {
  text-align: center;
  color: white;
}
main .portfolio .header h2 {
  padding: 5px 25px;
  margin-left: auto;
  margin-right: auto;
}
main .portfolio .header div {
  max-width: 85%;
  margin: auto;
  letter-spacing: 4px;
  font-size: 1.2em;
}
footer .contacts .header {
  text-align: center;
}
footer .contacts .header div {
  letter-spacing: 4px;
  font-size: 1.2em;
}
footer .contacts .header .underline {
  line-height: 3rem;
  border-bottom: 2px solid #000000;
  width: 60%;
  margin: auto;
  padding: 12px;
}
footer .contacts .item {
  text-align: center;
  margin-bottom: 2rem;
}
footer .contacts .item .ico {
  margin-bottom: 1rem;
}
footer .contacts .item .ico span i {
  color: #00AFF0;
}
footer .copyright {
  background-color: #222;
  color: white;
  margin-bottom: 0;
}
footer .copyright i {
  color: #FF6347;
  cursor: pointer;
}
@media only screen and (max-width: 992px) {
  .description,
  .photo,
  .personal {
    margin-bottom: 40px;
  }
}
@media only screen and (max-width: 576px) {
  h2 {
    box-shadow: 0 11px 0 white, 0 20px 20px black;
  }
  .portfolio h2 {
    box-shadow: 0 11px 0 #222, 0 20px 20px white;
  }
}
@media only screen and (max-width: 480px) {
  h2 {
    margin-bottom: 100px;
  }
  .personal {
    margin-bottom: 15px;
  }
}
